<template>
  <div id="userList">
    <div class="card">
      <h2 class="mb10">用户信息查看</h2>
      <Table border :columns="columns" :data="dataList" class="clear-both"></Table>
      <Page class="pull-right mt10" :total="page.total" @on-change="loadData($event)" show-total show-elevator :page-size="page.pageNum"></Page>
    </div>
  </div>
</template>

<script>
import { CONFIG } from '../services/config'
export default {
  name: 'userList',
  data () {
    return {
      loadingStatus: false,
      columns: [
        {
          title: '村居法律顾问姓名',
          key: 'lawName'
        },
        {
          title: '村居法律顾问性别',
          key: 'lawSex',
          render: (h, params) => {
            return h('div', (params.row.lawSex === '0' ? '男' : '女'))
          }
        },
        {
          title: '联系电话',
          key: 'lawMobileno'
        },
        {
          title: '所在单位',
          key: 'lawCompany'
        },
        {
          title: '服务社区（村）',
          key: 'communityId'
        },
        {
          title: '注册日期',
          key: 'tripNums'
        }
      ],
      dataList: [],
      page: {
        total: 0,
        pageNum: 10, // 每页几条
        pageNo: 1 // 页码
      }
    }
  },
  methods: {
    loadData (pageNo) {
      if (pageNo) {
        this.page.pageNo = pageNo
      }
      this.$http.post(CONFIG.lawyerList.url, {
        head: CONFIG.lawyerList.head,
        body: {
          pageNum: this.page.pageNum,
          pageNo: this.page.pageNo,
          audStt: 1 // 未审核
        }
      }, {
        headers: {
          tranCode: CONFIG.lawyerList.head.tranCode
        }
      }).then(res => {
        res = res.data
        const head = res.head
        const body = res.body
        if (head.errorCode === CONFIG.SUCCESS_CODE) {
          this.dataList = body.lawInfos
          this.page.total = parseInt(body.total)
        } else {
          this.$Message.error('查询失败!' + head.errorMsg ? head.errorMsg : '')
        }
      }, res => {
        this.$Message.error('查询失败!')
      })
    }
  },
  created: function () {
    this.loadData()
  }
}
</script>



